import axios from 'axios'
import { LoadingBar } from 'view-design';
import qs from 'qs'

const isDev = process.env.NODE_ENV === 'development'

const ajax = axios.create({
    // baseURL: isDev ? "http://192.168.1.65:8000" : "http://192.168.1.65:8000"
    baseURL: isDev ? "http://222.202.92.120:8081" : "http://222.202.92.120:8081"
})


// 拦截器
ajax.interceptors.request.use((config) => {
    LoadingBar.start();
    return config
})

ajax.interceptors.response.use((resp) => {
    LoadingBar.finish();
    if (resp.status === 200) {
        return resp.data
    }
})

// 用户登录
export const userLogin = (username, password, headers) => {
    return ajax({
        url: '/api/userLogin',
        method: 'post',
        responseType: 'json',
        data: qs.stringify({ 'username': username, 'password': password }),
        headers: {
            'X-CSRFToken': headers
        }
    })
}

// 用户注销
export const userLogout = () => {
    return ajax.get('/api/userLogout')
}

// 修改密码
export const modifyPwd = (user, newPwd, headers) => {
    return ajax({
        url: '/api/modifyPassword',
        method: 'post',
        responseType: 'json',
        data: qs.stringify({ 'user': user, 'newPwd': newPwd }),
        headers: {
            'X-CSRFToken': headers
        }
    })
}

// 选择类型
export const responseType = () => {
    return ajax.get('/api/responseTypes')
}
