import SysHeader from '@/components/SysHeader'
const Login = () => import('@/views/SysLogin')
const Index = () => import('@/views/SysIndex')
const Reagent = () => import('@/views/SysReagent')
const ReagentDetail = () => import('@/views/SysReagentDetail')

const Websocket = () => import('@/views/Websocket')
const upload = () => import('@/views/SysUpload')

export default [
    {
        path: '/',
        redirect: '/systemIndex',

    }, {
        path: '/systemIndex',
        name: 'systemIndex',
        components: {
            default: Index,
            header: SysHeader
        },
        meta: {
            requireAuth: true
        }

    }, {
        path: '/systemLogin',
        name: 'systemLogin',
        components: {
            login: Login,
        }
    }, {
        path: '/systemReagent',
        name: 'systemReagent',
        components: {
            default: Reagent,
            header: SysHeader
        },
        meta: {
            requireAuth: true
        }
    }, {
        path: '/one_reagent',
        name: 'system_reagent',
        components: {
            default: ReagentDetail,
            header: SysHeader
        },
        meta: {
            requireAuth: true
        }
    }, {
        path: "/websocket",
        name: "websocket",
        components: {
            default: Websocket,
        },
        meta: {
            requireAuth: true
        }
    },{
        path: "/upload",
        name: "upload",
        components: {
            default: upload,
            header: SysHeader
        },
        meta: {
            requireAuth: true
        }
    }
]
